import React from "react";
import {
  Navbar,
  Text,
  Button,
  Dropdown,
  Link,
  Switch,
  useTheme,
} from "@nextui-org/react";
import { useTheme as useNextTheme } from "next-themes";
import { Image, Image2, Setting } from "react-iconly";

export default function () {
  const { setTheme } = useNextTheme();
  const { isDark, type } = useTheme();

  return (
    <Navbar isBordered>
      <Navbar.Brand>
        <Text b>Koru</Text>
      </Navbar.Brand>
      <Navbar.Content>
        <Link href="/#/" color="inherit">
          Home
        </Link>
        <Link href="/#/login" color="inherit">
          Login
        </Link>
        <Dropdown>
          <Dropdown.Button auto light>
            <Setting set="bold" primaryColor={isDark ? "white" : "black"} />
          </Dropdown.Button>
          <Dropdown.Menu>
            <Dropdown.Item key="new">New file</Dropdown.Item>
            <Dropdown.Item
              key="theme"
              icon={
                isDark ? (
                  <Image2 set="bold" primaryColor="white" />
                ) : (
                  <Image set="bold" primaryColor="black" />
                )
              }
            >
              <Text
                as={Link}
                onPress={() => setTheme(type === "dark" ? "light" : "dark")}
              >
                Change theme
              </Text>
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </Navbar.Content>
    </Navbar>
  );
}
